.cta {
  padding: 7rem 0;
}
.cta:not(:last-child) {
  margin-bottom: 4.7rem;
}
.cta__container {
  display: flex;
  grid-template-columns: repeat(10, 1fr);
  gap: 4rem;
  padding: 0 14.5rem;
}
.cta__content {
  width: 100%;
  max-width: 59.4rem;
  flex-shrink: 0;
}
.cta__right {
  margin-top: 1.6rem;
  width: 100%;
  background-color: #fff;
  padding: 5rem;
  padding-top: 4rem;
  padding-bottom: 4.8rem;
  border-radius: 3rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.cta__img {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 66.6rem;
  mask-image: url("./../img/cta-mask.svg");
  mask-size: auto 100%;
  mask-repeat: no-repeat;
  mask-position: center;
}
.cta__img img {
  width: 100%;
  height: 100%;
}
.cta__link {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 100%;
  color: var(--black);
}
.cta__subtitle {
  font-size: 5rem;
  font-weight: 600;
  line-height: 1;
}
.cta__subtitle span {
  color: var(--blue);
}
.cta__title {
  margin-bottom: 3rem;
}

.cta-b {
  padding: 7rem 16.4rem;
  margin-bottom: 13rem;
  position: relative;
}
.cta-b::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: -48.2rem;
  top: -19.7rem;
  transform: rotateZ(-90deg);
  width: 124rem;
  height: 124rem;
  background-image: url("./../img/lines/main4.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.cta-b__container {
  background-color: #fff;
  padding: 8rem;
  padding-top: 6.6rem;
  padding-bottom: 7.6rem;
  border-radius: 3rem;
  position: relative;
}
.cta-b__title {
  margin-bottom: 1.4rem;
}
.cta-b__desc {
  font-weight: 500;
  margin-bottom: 5rem;
}
.cta-b__content {
  max-width: 55%;
}
.cta-b__form {
  max-width: 51.2rem;
}
.cta-b__form .cta-form__input {
  padding: 0 1.8rem;
  height: 5.7rem;
}
.cta-b__form .cta-form__input:not(:last-child) {
  margin-bottom: 2rem;
}
.cta-b__form .cta-form__input:last-of-type {
  margin-bottom: 5.7rem;
}
.cta-b__img {
  position: absolute;
  z-index: 4;
  right: -3.5rem;
  bottom: -0.5rem;
  width: 71.3rem;
  aspect-ratio: 1/1;
}
.cta-b__img::before {
  content: "";
  position: absolute;
  z-index: -1;
  background-color: var(--cyan);
  width: 94.6%;
  aspect-ratio: 1/1;
  bottom: -4.6rem;
  left: -5.9rem;
  mask-image: url("./../img/cta-b-mask.svg");
  mask-size: auto 100%;
}
.cta-b__img img {
  width: 100%;
  height: 100%;
}

.cta-form__wrapper:not(:last-child) {
  margin-bottom: 2.7rem;
}
.cta-form__wrapper:last-of-type {
  margin-bottom: 7.7rem;
}
.cta-form__inwrapper {
  position: relative;
}
.cta-form__inwrapper::after {
  content: attr(data-after);
  font-weight: 500;
  font-size: 2rem;
  line-height: 100%;
  color: var(--black);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 3rem;
}
.cta-form__label {
  display: block;
  margin-bottom: 0.9rem;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 92%;
  color: var(--black);
}
.cta-form__input {
  display: block;
  width: 100%;
  height: 6rem;
  border: 2.5px solid var(--black);
  border-radius: 1.5rem;
  padding: 0 2.82rem;
  font-weight: 500;
  font-size: 2rem;
  line-height: 92%;
  color: var(--black);
  background: none;
}
.cta-form__btn {
  width: 100%;
  padding: 1.3rem 0 1.9rem;
  font-size: 2rem;
  border-radius: 1.5rem;
}
.cta-form__btn:not(:last-child) {
  margin-bottom: 1.5rem;
}
.cta-form__agree {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: var(--black);
}
.cta-form__agree a {
  color: inherit;
  text-decoration: underline;
}

.benefits {
  padding: 7rem 0;
  margin-bottom: 5rem;
  position: relative;
}
.benefits::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: -70.2rem;
  top: -17.7rem;
  width: 145rem;
  height: 145rem;
  transform: rotateZ(90deg) rotateX(180deg);
  background-image: url("./../img/lines/main1.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.benefits::after {
  content: "";
  position: absolute;
  z-index: -1;
  right: -58.8rem;
  bottom: -53rem;
  width: 145rem;
  height: 143rem;
  transform: rotateZ(90deg);
  background-image: url("./../img/lines/main1.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.benefits__title {
  margin-bottom: 5rem;
}
.benefits__bottom {
  text-align: center;
}
.benefits__btn {
  padding: 1.3rem 0 1.9rem;
  width: 32.8rem;
  border-radius: 1.5rem;
  font-size: 2rem;
}
.benefits__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  margin-bottom: 6rem;
}

.ben-item {
  display: flex;
  gap: 1rem;
  min-height: 40rem;
  background-color: #fff;
  border-radius: 3rem;
  overflow: hidden;
  position: relative;
}
.ben-item__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5.3rem;
  padding-top: 4.1rem;
  padding-bottom: 4.6rem;
  width: 100%;
  max-width: 49rem;
  flex-shrink: 0;
}
.ben-item__title {
  margin: 0;
  margin-bottom: 1.8rem;
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 120%;
  color: var(--black);
}
.ben-item__desc {
  line-height: 123.5%;
}
.ben-item__link {
  margin-top: auto;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 120%;
  color: var(--black);
}
.ben-item__img {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 43.4rem;
  flex-shrink: 0;
  mask-image: url("./../img/benefits/mask.svg");
  mask-size: auto 100%;
  mask-repeat: no-repeat;
  background-position: center;
}
.ben-item__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: 0.2s ease-in;
}
@media (any-hover: hover) {
  .ben-item:hover .ben-item__img img {
    transform: scale(1.05);
  }
}

.news-section {
  padding: 6.7rem 0;
}
.news-section:not(:last-child) {
  margin-bottom: 6.3rem;
}
.news-section__title {
  margin-bottom: 5rem;
}
.news-section__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
  margin-bottom: 5.5rem;
}
.news-section__bottom {
  text-align: center;
}
.news-section__link {
  padding-top: 1.3rem;
  padding-bottom: 1.9rem;
  font-size: 2rem;
  border-radius: 1.5rem;
  width: 32.8rem;
}

.news-item {
  position: relative;
}
.news-item__img {
  height: 40rem;
  border-radius: 3rem;
  overflow: hidden;
  margin-bottom: 2.15rem;
}
.news-item__img img {
  width: 100%;
  height: 100%;
}
.news-item__link::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 2;
}
.news-item__title {
  margin-bottom: 1rem;
}
.news-item__date {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 120%;
  color: var(--black);
}

.footer {
  padding: 6rem 0;
  padding-top: 5.2rem;
  background-color: #fff;
}
.footer__container {
  display: flex;
